@use "setting" as *;

//! destyle.css v2.0.2 | MIT License | https://github.com/nicolas-cusan/destyle.css */

table,
blockquote,
address,
pre,
form,
dl,
dd {
    margin: 0;
}
pre {
    font-family: monospace, monospace; // 1 */
    font-size: inherit; // 2 */
}

address {
    font-style: inherit;
}

// Text-level semantics */
// ============================================ */


abbr[title] {
    text-decoration: underline; // 2 */
    text-decoration: underline dotted; // 2 */
}


small {
    font-size: 80%;
}


// Forms */
// ============================================ */

button,
input,
optgroup,
select,
textarea {
    -webkit-appearance: none;
    appearance: none;
    vertical-align: middle;
    color: inherit;
    font: inherit;
    background: transparent;
    padding: 0;
    margin: 0;
    outline: 0;
    border-radius: 0;
    text-align: inherit;
}

[type="checkbox"] {
    -webkit-appearance: checkbox;
    appearance: checkbox;
}

[type="radio"] {
    -webkit-appearance: radio;
    appearance: radio;
}

button,
input {
    // 1 */
    overflow: visible;
}

button,
select {
    // 1 */
    text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
    cursor: pointer;
    -webkit-appearance: none;
    appearance: none;
}

button[disabled],
[type="button"][disabled],
[type="reset"][disabled],
[type="submit"][disabled] {
    cursor: default;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
    border-style: none;
    padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
    outline: 1px dotted ButtonText;
}

option {
    padding: 0;
}

fieldset {
    margin: 0;
    padding: 0;
    min-width: 0;
}

legend {
    color: inherit; // 2 */
    display: table; // 1 */
    max-width: 100%; // 1 */
    padding: 0; // 3 */
    white-space: normal; // 1 */
}

progress {
    vertical-align: baseline;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
    height: auto;
}

[type="search"] {
    outline-offset: -2px; // 1 */
}

[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}

::-webkit-file-upload-button {
    -webkit-appearance: button; // 1 */
    font: inherit; // 2 */
}

label[for] {
    cursor: pointer;
}

// Interactive */
// ============================================ */

details {
    display: block;
}

summary {
    display: list-item;
}

[contenteditable] {
    outline: none;
}

// Table */
// ============================================ */

table {
    border-collapse: collapse;
    border-spacing: 0;
}

caption {
    text-align: left;
}

td,
th {
    vertical-align: top;
    padding: 0;
}

th {
    text-align: left;
}
